<template>
    <div class="f-datepicker-example">
        <pre>
        Model: {{ model }}
        </pre>
        <pre v-if="toggleSpaceAboveAnchor">
            {{ "\n".repeat(14) }}
        </pre>
        <button type="button" @click="onClickToggleSpaceAboveAnchorButton">
            Toggle space above datepicker anchor (on/off)
        </button>
        <f-datepicker id="normal" v-model="model" v-test="'normal'" :options="datepickerOptions">
            Pick a date
        </f-datepicker>
        <button type="button" @click="onClickToggleSpaceBelowAnchorButton">
            Toggle space under datepicker anchor (on/off)
        </button>
        <pre v-if="toggleSpaceBelowAnchor">
            {{ "\n".repeat(14) }}
        </pre>
    </div>
</template>

<script>
import { FDatepicker } from "@fkui/vue";
import { FDatepickerComponentOptions } from "@fkui/vue";

export default {
    name: "FDatepickerExample",
    components: { FDatepicker },
    data() {
        return {
            model: "",
            datepickerOptions: new FDatepickerComponentOptions({
                showWeekNumbers: false,
            }),
            toggleSpaceAboveAnchor: false,
            toggleSpaceBelowAnchor: false,
        };
    },
    methods: {
        onClickToggleSpaceAboveAnchorButton() {
            this.toggleSpaceAboveAnchor = !this.toggleSpaceAboveAnchor;
        },
        onClickToggleSpaceBelowAnchorButton() {
            this.toggleSpaceBelowAnchor = !this.toggleSpaceBelowAnchor;
        },
    },
};
</script>
