









































































import Vue from "vue";
import {
    FDatepickerField,
    FTextField,
    FRadioGroup,
    FRadioGroupField,
    IValidationForm,
    getHTMLElementFromVueRef,
} from "@fkui/vue";
import { Weekday } from "@fkui/date";
import { FDate } from "@fkui/date";
import { ValidationService } from "@fkui/logic";
export default Vue.extend({
    name: "FDatePickerFieldTest",
    components: {
        FDatepickerField,
        FTextField,
        FRadioGroup,
        FRadioGroupField,
        IValidationForm,
    },
    data() {
        return {
            date: "",
            completed: false,
            name: "",
            certificate: undefined,
            minDate: FDate.now().addMonths(-3).toString(),
            maxDate: FDate.now().addMonths(2).toString(),
            invalidWeekend: [Weekday.SATURDAY, Weekday.SUNDAY],
        };
    },
    mounted() {
        getHTMLElementFromVueRef(this.$refs.order).focus();
    },
    methods: {
        async onRestart(): Promise<void> {
            ValidationService.clearAllStates();
            this.certificate = undefined;
            this.date = "";
            this.name = "";
            this.completed = false;
            await this.$nextTick();
            getHTMLElementFromVueRef(this.$refs.order).focus();
        },
        async onSubmit(): Promise<void> {
            this.completed = true;
            await this.$nextTick();
            getHTMLElementFromVueRef(this.$refs.done).focus();
        },
    },
});
