

























import Vue from "vue";
import { FModal } from "@fkui/vue-runtime";
export default Vue.extend({
    components: { FModal },
    data() {
        return {
            isOpen: false,
        };
    },
    methods: {
        openModal() {
            this.isOpen = true;
        },
        closeModal() {
            this.isOpen = false;
        },
    },
});
