<template>
    <f-form-modal
        :size="size"
        :data-test="testId"
        :use-error-list="false"
        :is-open="isOpen"
        :value="value"
        :before-submit="onBeforeSubmit"
        @submit="onSubmit"
        @cancel="onCancel"
        @close="onClose"
    >
        <template #header>Träutensilierna</template>
        Träutensilierna i ett tryckeri äro ingalunda en oviktig faktor, för trevnadens, ordningens och ekonomiens
        upprätthållande, och dock är det icke sällan som sorgliga erfarenheter göras på grund af det oförstånd med
        hvilket kaster, formbräden och regaler tillverkas och försäljas Kaster som äro dåligt hopkomna och af
        otillräckligt.
        <template #error-message>Oj, du har glömt fylla i något. Gå till:</template>
        <template #input-text-fields>
            <f-text-field
                v-model="value.field1"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                class="field1"
            >
                Förnamn
            </f-text-field>
            <f-text-field
                v-model="value.field2"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                class="field2"
            >
                Efternamn
            </f-text-field>
        </template>
    </f-form-modal>
</template>

<script>
import { FFormModal, FTextField } from "@fkui/vue";

export default {
    name: "ExampleModal",
    components: { FFormModal, FTextField },
    props: {
        isOpen: {
            type: Boolean,
            default: false,
            required: false,
        },
        size: {
            default: "",
            type: String,
        },
        testId: {
            type: String,
            default: "",
            required: false,
        },
    },
    data: () => {
        return {
            value: {
                field1: "",
                field2: "",
            },
        };
    },
    methods: {
        onSubmit(event) {
            this.$emit("submit", event);
        },
        onCancel(event) {
            this.$emit("cancel", event);
        },
        onClose(event) {
            this.$emit("close", event);
        },
        onBeforeSubmit() {
            // eslint-disable-next-line no-console
            console.log(Date.now(), "onBeforeSubmit consumer");
        },
    },
};
</script>
