<template>
    <div>
        <h2>Exempel 1 av 3</h2>

        <f-radio-group name="more-questions" class="radio-button-group">
            <template #label> Vill du svar på mer frågor? </template>

            <f-radio-group-field v-model="moreQuestions" :value="true"> Ja tack </f-radio-group-field>

            <i-animate-expand :expanded="moreQuestions">
                <f-radio-group class="radio-button-group__indent" name="are-you-sure">
                    <template #label> Är du säker? </template>

                    <f-radio-group-field v-model="areYouSure" :value="true"> Ja, visa mer </f-radio-group-field>

                    <i-animate-expand :expanded="areYouSure">
                        <f-text-field v-validation.maxLength="20" class="radio-button-group__indent"
                            >Vad tyckte du?</f-text-field
                        >
                    </i-animate-expand>

                    <f-radio-group-field v-model="areYouSure" :value="false"> Nej tack </f-radio-group-field>
                </f-radio-group>
            </i-animate-expand>

            <f-radio-group-field v-model="moreQuestions" :value="false"> Nej tack </f-radio-group-field>
        </f-radio-group>

        <h2>Exempel 2 av 3</h2>
        <pre>Animerad expanding med rekommenderade attribut satta</pre>

        <button type="button" class="button" @click="isExpanded = !isExpanded">Öppna/stäng animering</button>
        <input v-model="isAnimated" type="checkbox" />Animera <input v-model="hasOpacity" type="checkbox" />Opacitet
        <input v-model="useVShow" type="checkbox" />Use v-show instead of v-if
        <select v-model="style" class="select-field__select">
            <option value="height: 200px; background: hotpink">200px höjd</option>
            <option value="height: 600px; background: cyan">600px höjd</option>
            <option value="height: 1200px; background: yellow">1200px höjd</option>
        </select>
        <pre>Callback: {{ callbackInfo }}</pre>
        <pre>Finns innehålls-div i DOM: {{ contentDivInDOM }}</pre>

        <i-animate-expand
            :expanded="isExpanded"
            :opacity="hasOpacity"
            :animate="isAnimated"
            :use-v-show="useVShow"
            :before-animation="beforeAnimationCallback"
            :after-animation="afterAnimationCallback"
        >
            <!-- [html-validate-disable-next no-inline-style] -->
            <div ref="content-div" :style="style">Ett animerat innehåll kan visas här.</div>
        </i-animate-expand>

        <h2>Exempel 3 av 3</h2>
        <pre>Animerad expandering kan nästlas</pre>

        <button type="button" class="button" @click="isExpanded1 = !isExpanded1">Öppna/stäng animering</button>
        <i-animate-expand :expanded="isExpanded1">
            <div style="background-color: yellow">
                Klicka för att öppna ytterligare en panel
                <button type="button" class="button" @click="isExpanded2 = !isExpanded2">Öppna/stäng panel 2</button>

                <i-animate-expand :expanded="isExpanded2">
                    <div style="background-color: hotpink; height: 200px">Här kommer ytterligare lite info...</div>
                </i-animate-expand>
            </div>
        </i-animate-expand>
    </div>
</template>

<script>
import { IAnimateExpand, FRadioGroup, FRadioGroupField, FTextField } from "@fkui/vue-runtime";
export default {
    namne: "IAnimateExpandExample",
    components: { IAnimateExpand, FRadioGroup, FRadioGroupField, FTextField },
    data() {
        return {
            moreQuestions: "",
            areYouSure: "",
            isExpanded: false,
            isAnimated: true,
            useVShow: false,
            hasOpacity: true,
            style: "height: 200px; background: hotpink",
            callbackInfo: "",
            isExpanded1: false,
            isExpanded2: false,
            contentDivInDOM: false,
        };
    },
    methods: {
        beforeAnimationCallback() {
            /* eslint-disable-next-line no-console */
            console.log("Before animation callback");
            this.callbackInfo = "Before animation callback";
        },
        afterAnimationCallback() {
            /* eslint-disable-next-line no-console */
            console.log("After animation callback");
            this.callbackInfo = "After animation callback";

            setTimeout(() => {
                this.contentDivInDOM = this.$el.contains(this.$refs["content-div"]);
            });
        },
    },
};
</script>
