<template>
    <div>
        <f-validation-group key="unik-nyckel" v-model="favoritGrupp">
            <f-text-field id="kaka" v-validation.required maxlength="100">Favoritkaka</f-text-field>

            <f-radio-group id="val" v-validation.required v-test="'val'" name="val">
                <template #label>Frukt eller godis?</template>

                <f-radio-group-field id="val-frukt" v-model="val" v-test="'val-frukt'" :value="true">
                    Frukt
                </f-radio-group-field>
                <f-radio-group-field id="val-godis" v-model="val" v-test="'val-godis'" :value="false">
                    Godis
                </f-radio-group-field>
            </f-radio-group>

            <div v-if="val === true">
                <f-text-field id="frukt" v-model="frukt" v-validation.required v-test="'frukt'" maxlength="100"
                    >Favoritfrukt</f-text-field
                >
            </div>
            <div v-else>
                <f-text-field id="godis" v-model="godis" v-validation.required v-test="'godis'" maxlength="100"
                    >Favoritgodis</f-text-field
                >
            </div>
        </f-validation-group>

        <button type="button" class="button" :disabled="!favoritGrupp.isValid">Lägg till något nytt</button>

        <p>isValid:</p>
        <pre data-test="favorit-grupp-is-valid">{{ favoritGrupp.isValid }}</pre>
        <p>componentsWithError.length:</p>
        <pre data-test="favorit-grupp-error-count">{{
            favoritGrupp.componentsWithError ? favoritGrupp.componentsWithError.length : 0
        }}</pre>
        <p>componentsWithError:</p>
        <pre>{{ favoritGrupp.componentsWithError }}</pre>
        <p>componentCount:</p>
        <pre>{{ favoritGrupp.componentCount }}</pre>
    </div>
</template>

<script>
import { FValidationGroup, FTextField, FRadioGroup, FRadioGroupField } from "@fkui/vue-runtime";

export default {
    name: "OptimizationExample",
    components: { FTextField, FValidationGroup, FRadioGroup, FRadioGroupField },
    data() {
        return {
            favoritGrupp: {},
            val: true,
            kaka: "",
            frukt: "",
            godis: "",
        };
    },
};
</script>
