











import Vue from "vue";
import { FNavigationMenu, NavigationMenuItem } from "@fkui/vue-runtime";

function generateExampleLabelsAndRoutes(nbRoutes: number): NavigationMenuItem[] {
    const res: NavigationMenuItem[] = [];
    for (let i = 0; i < nbRoutes; i++) {
        res.push({ label: `label${i}`, route: `ROUTE_${i}` });
    }
    return res;
}

export default Vue.extend({
    components: { FNavigationMenu },
    data() {
        return {
            routes: generateExampleLabelsAndRoutes(10),
            selectedRoute: "",
        };
    },
    methods: {},
});
