<template>
    <f-crud-dataset
        v-model="items"
        data-test="f-crud-dataset-list-example"
        :before-create="beforeCreate"
        :before-submit="beforeSubmit"
        :before-validation="beforeValidation"
        :on-cancel="onCancel"
        @created="saveModel"
        @updated="saveModel"
        @deleted="saveModel"
    >
        <template #default>
            <f-list :items="items" element-id="example" title="Pre selected list example" key-attribute="id" selectable>
                <template #default="{ item }">
                    <h3>{{ item.name }}</h3>
                    <div class="row">
                        <div class="col col--sm-9">
                            <dl>
                                <dt>Kortnummer</dt>
                                <dd>{{ item.id }}</dd>
                            </dl>
                            <dl>
                                <dt>Rättigheter</dt>
                                <dd>{{ item.permission }}</dd>
                            </dl>
                        </div>
                        <div class="col col--sm-3">
                            <!-- [html-validate-disable-next no-inline-style] -->
                            <ul style="list-style-type: none" class="button-list">
                                <li>
                                    <f-crud-button action="modify" :item="item" icon label />
                                </li>
                                <li>
                                    <f-crud-button action="delete" :item="item" icon label />
                                </li>
                            </ul>
                        </div>
                    </div>
                </template>
                <template #screenreader="{ item }"> Kortnummer {{ item.id }} </template>
            </f-list>
        </template>

        <template #add="{ item }">
            Kortnummer {{ item.id }}
            <f-text-field
                v-model="item.name"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                type="text"
                name="name"
            >
                Namn
            </f-text-field>
            <f-select-field id="permission-type-add" v-model="item.permission" name="permission-type">
                <template #label>Rättighet</template>
                <option value="Läs">Läs</option>
                <option value="Skriv">Skriv</option>
            </f-select-field>
        </template>

        <template #modify="{ item }">
            <f-text-field
                v-model="item.name"
                v-validation.required.maxLength="{ maxLength: { length: 32 } }"
                type="text"
                name="name"
            >
                Namn
            </f-text-field>
            <f-select-field id="permission-type-modify" v-model="item.permission" name="permission-type">
                <template #label>Rättighet</template>
                <option value="Läs">Läs</option>
                <option value="Skriv">Skriv</option>
            </f-select-field>
        </template>

        <template #delete="{ item }">
            Vill du verkligen radera personen {{ item.name }} med kortnummer {{ item.id }}
        </template>
    </f-crud-dataset>
</template>

<script>
import { FCrudDataset, FCrudButton, FList, FTextField, FSelectField } from "@fkui/vue";

export default {
    name: "ExampleApp",
    components: {
        FCrudDataset,
        FCrudButton,
        FList,
        FTextField,
        FSelectField,
    },
    data() {
        return {
            items: [
                { id: "40044707", name: "Tommy Settergren", permission: "Skriv", selected: false },
                { id: "66132046", name: "Annika Settergren", permission: "Läs", selected: false },
                { id: "66124424", name: "Fröken Prysselius", permission: "Skriv", selected: false },
                { id: "40042500", name: "Dunder Karlsson", permission: "Skriv", selected: true },
            ],
        };
    },
    methods: {
        // Förpopulera ett objekt med värden
        beforeCreate() {
            return {
                id: this.getMaxId() + 1,
                name: "",
                permission: "Läs",
                selected: false,
            };
        },
        beforeSubmit() {
            window.alert("beforeSubmit");
        },
        beforeValidation() {
            window.alert("beforeValidation");
        },
        onCancel() {
            window.alert("onCancel");
        },
        getMaxId() {
            let maxId = 0;
            for (let i = 0; i < this.items.length; i++) {
                if (this.items[i].id > maxId) {
                    maxId = this.items[i].id;
                }
            }
            return maxId;
        },
        saveModel(row) {
            /* eslint-disable-next-line no-console */
            console.log("Post model to backend", row);
        },
    },
};
</script>
