<template>
    <div class="f-form-modal-example">
        <button data-test="form-modal-example-button" type="button" class="button button--secondary" @click="onClick">
            Öppna Modal
        </button>
        <div v-if="isOpen" data-test="form-modal-example" class="f-form-modal-example">
            <example-modal :is-open="isOpen" @submit="onSubmit" @cancel="onCancel" @close="onClose"></example-modal>
        </div>
        <pre>Modalen stängdes med resultatet:</pre>
        <pre v-show="field1 || field2">Förnamn: {{ field1 }}, Efternamn: {{ field2 }}</pre>
    </div>
</template>

<script>
import ExampleModal from "./ExampleModal";

export default {
    name: "FFormModalExample",
    components: { ExampleModal },
    props: {},
    data: () => {
        return {
            isOpen: false,
            field1: "",
            field2: "",
        };
    },
    methods: {
        onClick() {
            this.isOpen = true;
        },
        onClose() {
            this.isOpen = false;
        },
        onSubmit(event) {
            this.field1 = event.data.field1;
            this.field2 = event.data.field2;
            window.alert("onSubmit");
        },
        onCancel() {
            this.resetFields();
            window.alert("onCancel");
        },
        resetFields() {
            this.field1 = "";
            this.field2 = "";
        },
    },
};
</script>
