<template>
    <div>
        <h2>Exempel med lista och rubrik angivet</h2>
        <f-sort-filter-dataset
            :data="items"
            :sortable-attributes="{ name: 'Namn', permission: 'Rättighet', age: 'Ålder' }"
            @datasetSorted="getSortedData"
        >
            <template #header="{ slotClass }">
                <h3 :class="slotClass">Rättigheter</h3>
            </template>
            <template #default="{ sortFilterResult }">
                <f-list v-test="'selectable-list'" title="Selectable list" key-attribute="id" :items="sortFilterResult">
                    <template #default="{ item }">
                        <h3>{{ item.name }}</h3>
                        <p>
                            Kortnummer:
                            <em>{{ item.id }}</em>
                            <br />Skrivrättighet:
                            <em>{{ item.permission }}</em>
                            <br />
                        </p>
                    </template>
                    <template #screenreader="{ item }">Användare {{ item.name }}</template>
                </f-list>
            </template>
        </f-sort-filter-dataset>
        <h2>Exempel med tabell och förvald sortering</h2>

        <f-sort-filter-dataset
            v-test="'sort-filter-table-example'"
            :data="items"
            default-sort-attribute="name"
            :default-sort-ascending="true"
            :sortable-attributes="{ name: 'Namn', permission: 'Rättighet', age: 'Ålder' }"
            @datasetSorted="getSortedData"
        >
            <template #header="{ slotClass }">
                <h3 :class="slotClass">Rättigheter</h3>
            </template>
            <template #default="{ sortFilterResult }">
                <f-interactive-table :rows="sortFilterResult" striped key-attribute="id">
                    <template #caption>
                        <span class="sr-only">Behörigheter</span>
                    </template>
                    <template #default="{ row }">
                        <f-table-column name="id" title="Kortid" type="text" expand>{{ row.id }}</f-table-column>
                        <f-table-column name="name" title="Namn" type="text" expand>{{ row.name }}</f-table-column>
                        <f-table-column name="age" title="Ålder" type="numeric" shrink>{{ row.age }}</f-table-column>
                        <f-table-column name="permission" title="Rättighet" type="text" shrink>{{
                            row.permission
                        }}</f-table-column>
                    </template>
                </f-interactive-table>
            </template>
        </f-sort-filter-dataset>

        <h2>Exempel med egen presentation och utan rubrik</h2>

        <f-sort-filter-dataset
            :data="items"
            :sortable-attributes="{ name: 'Namn', permission: 'Rättighet', age: 'Ålder' }"
            @datasetSorted="getSortedData"
        >
            <template #default="{ sortFilterResult }">
                <!-- [html-validate-disable-next no-inline-style] -->
                <pre
                    v-for="item in sortFilterResult"
                    :key="item.id"
                    style="display: inline-block; border: 1px solid black; padding: 10px; margin: 10px"
                    >{{ item }}</pre
                >
            </template>
        </f-sort-filter-dataset>
    </div>
</template>

<script>
import { FSortFilterDataset, FInteractiveTable, FTableColumn, FList } from "@fkui/vue";

export default {
    name: "FSortFilterDatasetExample",
    components: { FSortFilterDataset, FInteractiveTable, FTableColumn, FList },
    data() {
        return {
            items: [
                { id: "40051234", name: "Tommy Settergren", permission: "Skriv", age: 22 },
                { id: "40051235", name: "Annika Settergren", permission: "Läs", age: null },
                { id: "40051236", name: "Fröken Prysselius", permission: "Skriv", age: 33 },
                { id: "40051241", name: "Dunder Karlsson", permission: "Skriv", age: 100 },
            ],
        };
    },
    methods: {
        getSortedData(sortFilterResult) {
            // eslint-disable-next-line no-console
            console.log("Use sorted data", sortFilterResult);
        },
    },
};
</script>
